  import LoadinImg from '../../public/loadingImg'
  import Chat from '../../public/chat'
import Head from '../../public/head'
export default {
    name: 'refundDescirbe',
    data(){
        return{
          api: this.GLOBAL.baseURL,
          house:[],
          type:'',
          order_id:'',
          Rdata:'',
          price:'',
          isPrice:false,
          alerIndex:'',
          reason:'',
          imgUrl:'',
          userid:'',
          to_user_id:'',
          userName:'',
          other_img:'',
          isChat:false,
          isdiscus:false,
          content:'',
          wherIndex:''
        }
    },
    components:{
        Head,
      Chat,
      LoadinImg
    },
    methods:{
      closeDiscus(){
        this.isdiscus = false
        this.content = ''
      },
      changeOrder(){
        this.isPrice = !this.isPrice
        this.alerIndex = 'changeMoney'
      },
      changeMoney(){
        this.$http.post(this.api + '/PriceU',{
          token: localStorage.getItem('token'),
          order_id: this.order_id,
          total_price: this.price
        })
          .then(res=>{
            if(res.data.code == 1){
              this.$message({
                type: 'success',
                message: '修改成功！'
              })
              this.isPrice = false
              this.getOrder()
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                   localStorage.setItem('token',res.data.data)
                  this.changeMoney()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      goBack(){
        if(this.wherIndex){
          // this.$router.go(-1)
          this.$router.push({
            path: '/myPlay',
            query:{
              chooseIndex: this.wherIndex
            }
          })
        }else{
          this.$router.go(-1)
        }
      },
      getRefunc(){
        this.$http.post(this.api + '/RefundDTwo',{
          token: localStorage.getItem('token'),
          refund_id: this.order_id
        })
          .then(res=>{
            if(res.data.code == 1){
              this.Rdata = res.data.data
    
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getRefunc()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      //同意
      agreeOrder(){
        if(this.type == 3){
          this.$confirm('此操作将退还用户已支付金额!, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.agreeInformation()
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消'
            });
          });
        }
      },
      agreeInformation(){
        this.$http.post(this.api + '/AgreeRefund',{
          token: localStorage.getItem('token'),
          refund_id: this.order_id,
          type: 1,
          return_price: this.Rdata.total_price
        })
          .then(res=>{
            if(res.data.code == 1){
              this.$message({
                type: 'success',
                message: '退款成功！'
              })
              this.isPrice = false
              this.getRefunc()
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.agreeInformation()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      //拒绝
      refused(){
        if(this.type == 3){
          if(!this.reason){
            this.$message({
              type: 'info',
              message: '请输入您拒绝理由！'
            })
          }else{
            this.$http.post(this.api + '/AgreeRefund',{
              token: localStorage.getItem('token'),
              refund_id: this.order_id,
              type: 2,
              refuse_reason: this.reason
            })
              .then(res=>{
                if(res.data.code == 1){
                  this.$message({
                    type: 'success',
                    message:'您已拒绝退款申请!'
                  })
                  this.isPrice = false
                  this.isPrice = false
                  this.getRefunc()
                }else if(res.data.code == 3){
                  this.$http.post(this.api + '/home/index/token')
                    .then(res=>{
                      localStorage.setItem('token',res.data.data)
                      this.refused()
                    })
                }else if(res.data.code == 0){
                  alert(res.data.msg)
                }
              })
          }
        }
      },
      //退款
      changeErollA(){
        this.$router.push({
          path:'/playerReturn',
          query:{
            information: this.order_id
          }
        })
      },
      //修改
      Modify(){
        this.isPrice = !this.isPrice
        this.alerIndex = 'remake'
      },
      checkPrice(){
        this.$confirm('此操作将退给用户您输入的金额!, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.sendPrice()
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });
        });
      },
      sendPrice(){
        this.$http.post(this.api + '/AgreeRefund',{
          token: localStorage.getItem('token'),
          refund_id: this.order_id,
          return_price: this.price,
          type:1
        })
          .then(res=>{
            if(res.data.code == 1){
              this.$message({
                type: 'success',
                message: '退款成功！'
              })
              this.isPrice = false
              this.getRefunc()
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.sendPrice()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      toRefuse(){
        this.isPrice = !this.isPrice
        this.alerIndex = 'refused'
      },
      getOrder(){
        this.$http.post(this.api + '/OrderDTwo',{
          token: localStorage.getItem('token'),
          order_id: this.order_id,
          verson:2.0
        })
          .then(res=>{
            if(res.data.code == 1){
              this.Rdata = res.data.data
        
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getOrder()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      //评价
      toRevalution(){
        this.userName = this.Rdata.user.family_name+ this.Rdata.user.middle_name+ this.Rdata.user.name
        this.isdiscus = !this.isdiscus
      },
      saveDiscuss(){
        if( !this.content){
          this.$message({
            type: 'info',
            message: '请填写评价'
          })
        }else{
          this.$http.post(this.api + '/CommentPlannerS',{
            token: localStorage.getItem('token'),
            content: this.content,
            order_id: this.order_id
          })
            .then(res=>{
              if(res.data.code == 1){
                this.$message({
                  type: 'success',
                  message: '评价成功!'
                })
                this.isdiscus = false
                this.getOrder()
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.saveDiscuss()
                  })
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        }
      },
      contact(){

        this.isChat = true
        this.to_user_id = this.Rdata.user.user_id
        this.userName = this.Rdata.user.family_name+this.Rdata.user.middle_name+this.Rdata.user.name?this.Rdata.user.family_name+this.Rdata.user.middle_name+this.Rdata.user.name:'匿名用户'
        this.other_img = this.Rdata.user.head_image?this.Rdata.user.headimage.domain+this.Rdata.user.headimage.image_url:'../../../static/img/static/user.png'
      },
      chat(){
        this.isChat = false
      }
    },
  mounted(){
      if(this.type == '3'){
        this.getRefunc()
      }else{
          this.getOrder()
      }
 
  },
    created(){
      if(localStorage.getItem('userImg')){
        this.imgUrl = localStorage.getItem('userImg')
      }else{
        this.imgUrl = '../../../static/img/static/user.png'
      }
      this.type = this.$route.query.type
      this.order_id = this.$route.query.information
      this.wherIndex = this.$route.query.index
    }
}



